<template>
    <div class="container">
        <div class="signin">
            <div class="form col-lg-6 col-md-8 col-sm-12 mx-auto">
                <h1 class="text-left title">Login</h1>
                <p class="text-left">Email</p>
                <input type="email" name="email" v-model="email" class="input" placeholder="Email">
                <p class="text-left psw">Password</p>
                <input type="password" v-model="password" class="input" placeholder="Password">
                <router-link to="/signup">
                    <p class="text-left link-signup">Don't have an account yet?</p>
                </router-link>
                <button class="btn-sign" @click="handleSignin">Login</button>
            </div>
        </div>
    </div>
</template>

<script>

import firebase from 'firebase'

export default {
    name: 'signin',
    data() {
        return {
            email: '',
            password: '',
        }
    },
    methods: {
        handleSignin() {
            firebase.auth().signInWithEmailAndPassword(this.email, this.password)
            .then((user)=>{
                this.$router.push('/search')
            })
            .catch((e) => {
                alert(e.message)
            })
        }
    }
}
</script>



