<template>
    <div class="container">
        <div class="signup">
            <div class="form col-lg-6 col-md-8 col-sm-12 mx-auto">
                <h1 class="text-left title">Signup</h1>
                <p class="text-left">Email</p>
                <input type="email" name="email" v-model="email" class="input" placeholder="Email">
                <p class="text-left psw">Password</p>
                <input type="password" v-model="password" class="input" placeholder="Password">
                <button class="btn-sign" @click="handleSignup">Signup</button>
            </div>
        </div>
    </div>
</template>


<script>

import firebase from 'firebase'

export default {
    name: 'singup',
    data() {
        return {
            email: '',
            password: '',
        }
    },
    methods: {
        handleSignup() {
            firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
            .then((user)=>{
                this.$router.push('/search')
            })
            .catch((e) => {
                alert(e.message)
            })
        }
    }
}
</script>



